html {
  font-size: 100%;
  box-sizing: border-box; }

*::before,
*::after,
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  color: #323334; }

p {
  line-height: 1.7rem; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }
  a:hover {
    color: #2a62ff; }

ul {
  list-style-type: none; }

img {
  max-width: 100%;
  height: auto; }

button:focus,
input:focus,
select:focus {
  outline: 0; }

input::placeholder {
  color: #9098a2;
  font-weight: 300; }

select {
  padding: 0.5rem 0.5rem;
  border-radius: 0.5rem; }

.hidden {
  display: none !important; }

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1rem; }

.button {
  display: inline-block;
  background-color: #2a62ff;
  color: #fff;
  padding: 0.875rem 3.375rem;
  border: none;
  border-radius: 1.5rem;
  cursor: pointer; }

.input {
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
  border: none;
  padding-bottom: 0.5rem;
  border-bottom: 0.0625rem solid #9098a2; }

.error {
  position: absolute;
  width: 100%;
  font-size: 0.75rem;
  line-height: 1rem;
  color: #f00;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 0.25rem; }

.brand {
  font-size: 1.5rem;
  font-weight: 600; }

.welcome,
.information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center; }

.welcome {
  height: 80vh; }
  .welcome__title {
    font-size: 2.5rem;
    line-height: 2.875rem;
    margin-bottom: 3.5rem; }
  .welcome__text {
    font-size: 1.125rem;
    margin-bottom: 5rem; }
  .welcome .input-group {
    position: relative; }
  .welcome .button {
    margin: 2.5rem auto 0 auto;
    display: block; }

.information {
  padding: 4rem 1rem; }
  .information .greeting {
    align-self: flex-start;
    font-size: 2rem; }
  .information__text {
    font-size: 1.125rem;
    font-weight: 500;
    margin-top: 1.5rem; }
  .information__inputs {
    margin-top: 5rem; }
  .information__input:not(:nth-child(1)) {
    margin-top: 2rem; }
  .information__input .input {
    margin-top: 1rem; }
  .information__input:nth-of-type(4) .information__label {
    display: inline-block;
    margin-top: 1rem; }
    .information__input:nth-of-type(4) .information__label {
      margin-left: 0.25rem; }
    .information__input:nth-of-type(4) .information__label:nth-of-type(1) {
      margin-right: 0.5rem; }
  .information__label {
    font-size: 1.125rem;
    font-weight: 600;
    display: block; }
  .information label[for='field'] {
    margin-bottom: 1rem; }
  .information .button {
    margin: 2.5rem auto 0 auto;
    display: block; }
